@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --color: #6c5ce7;
}

* {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-transform: capitalize;
  transition: all .2s linear;
  text-decoration: none;
}

html {
  font-size: 62.5%;
}

body {
  overflow-x: hidden;
}



.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 1rem 2rem;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;

}

.header .logo {
  font-size: 2.5rem;
  color: #fff;
}

.header .logo i {
  padding: 0 1rem;
}

.home {
  min-height: 100vh;
  width: 100vw;
  background: linear-gradient(purple, var(--color));
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  text-align: center;
  padding: 0% 1rem;
  position: relative;
  overflow: hidden !important;
}

.header .navbar ul {
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.header .navbar ul li {
  margin: 0 1.5rem;
}

.header .navbar ul li a {
  font-size: 2rem;
  color: #fff;
}

.header .navbar ul li a:hover {
  color: #ccc;
}

.header .fa-bars {
  color: #fff;
  cursor: pointer;
  font-size: 3rem;
  display: none;
}

.heading {
  margin: 2rem;
  padding-top: 6rem;
  display: inline-block;
  font-size: 3.5rem;
  color: var(--color);
  position: relative;
  letter-spacing: .2rem;
}

.heading::before,
.heading::after {
  content: '';
  position: absolute;
  width: 2.5rem;
  height: 2.5rem;
  border-top: .4rem solid var(--color);
  border-left: .4rem solid var(--color);
}

.heading::before {
  top: 5.8rem;
  left: -2rem;
}

.heading::after {
  bottom: -.3rem;
  right: -2rem;
  transform: rotate(180deg);
}

.home .banner {
  color: #fff;
  font-size: 5rem;
  text-shadow: 0 .3rem .5rem rgba(0, 0, 0, 0.3);
}

.home .slogan {
  color: #eee;
  font-size: 2.5rem;
  font-weight: 400;
}

.home button {
  height: 4rem;
  width: 18rem;
  background: #fff;
  color: #444;
  cursor: pointer;
  outline: none;
  margin-top: 1rem;
  font-weight: 400;
  box-shadow: 0 .3rem .5rem rgba(0, 0, 0, 0.3);
}

.home button:hover {
  letter-spacing: .1rem;
}

.header .fa-bars,
.fa-times {
  display: none;
}

.home .fa-cog {
  position: absolute;
  font-size: 30rem;
  opacity: .15;
  color: #fff;
  animation: rotate 7s linear infinite;
}

.home .nut1 {
  top: 15%;
  left: -10%;
}

.home .nut2 {
  bottom: 15%;
  right: -10%;
  animation-direction: reverse;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

.about {
  min-height: 100vh;
  width: 100vw;
  text-align: center;
  overflow: hidden;
}

.about .row {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0 4rem;
}

.about .row .content {
  text-align: left;
}

.about .row .image img {
  width: 50vw;
}

.about .row .content h3 {
  font-size: 3rem;
  color: var(--color)
}

.about .row .content p {
  font-size: 1.5rem;
  color: #333;
  padding: 1rem 0;
}

.about::before,
.about::after {
  content: '';
  position: absolute;
  z-index: -1;
  opacity: .2;
}

