.btn {
  outline: none;
  border-radius: 5rem;
  background: var(--color);
  color: #fff;
  cursor: pointer;
  height: 3.5rem;
  width: 15rem;
  font-size: 1.7rem;
  box-shadow: 0 .2rem .5rem rgba(0, 0, 0, 0.3);
}

.btn:hover {
  letter-spacing: .1rem;
  opacity: .8;
}

.header.scrolled {
  background-color: #6C5CE7 !important;
  /* color when scrolled */
}

.service {
  text-align: center;
}

.service .row {
  margin: 2rem 0;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0 4rem;
}

.service .row .image img {
  width: 50vw;
}

.service .row .content {
  text-align: left;
  padding: 0 3rem;
}

.service .row .content h3 {
  font-size: 3rem;
  color: var(--color);
}

.service .row .content p {
  font-size: 1.5rem;
  color: #333;
  padding: 1rem 0;

}

.team {
  min-height: 100vh;
  width: 100vw;
  /* align-content: center; */
  text-align: center;
  background-color: #222;
}

.team .heading {
  color: #fff;
}

.team .heading::before,
.heading::after {
  border-color: var(--color)
}

.team .row {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2rem;
}

.team .row .card {
  height: 35rem;
  width: 25rem;
  background: #fff;
  text-align: center;
  margin: 7rem 2rem;
  position: relative;
  overflow: hidden;
  -webkit-box-reflect: below .2rem linear-gradient(transparent 70%, #0004);
}

.team .row .card .image {
  margin: 1rem 0;
  padding-top: 4rem;
}

.team .row .card .image img {
  height: 13rem;
  width: 13rem;
  border-radius: 50%;
  border: .5rem solid #fff;
  box-shadow: 0 0 .5rem;
  object-fit: cover;
}

.team .row .card .info h3 {
  font-size: 2rem;
  color: #333;
}

.team .row .card .info span {
  font-size: 1.8rem;
  color: var(--color);
}

.team .row .card .info .icons a {
  margin-top: 4rem;
  padding: 0 1rem;
  font-size: 2rem;
  color: #333;
}

.team .row .card .info .icons a:hover {
  color: var(--color);
}

.team .row .card::before,
.team .row .card::after {
  content: '';
  position: absolute;
  border-radius: 50%;
  height: 15rem;
  width: 15rem;
  z-index: -1;
}

.team .row .card::before {
  background: var(--color);
  top: -3rem;
  right: -4rem;
}

.team .row .card::after {
  background: #ccc;
  bottom: -3rem;
  left: -4rem;
}

.contact {
  min-height: 100vh;
  width: 100vw;
  text-align: center;
}

.contact .row {
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact .row .image img {
  /* height: 70vh; */
  width: 50vw;
}

.contact .row .form-container {
  width: 50%;
  text-align: left;
  padding: 0 5rem;
}

.contact .row .form-container input,
textarea,
button {
  outline: none;
  border: none;
  height: 4rem;
  background: none;
  border-radius: .5rem;
  box-shadow: .2rem .2rem .5rem rgba(0, 0, 0, 0.3);
  padding: 0 1rem;
  margin: 1rem 0;
  font-size: 1.6rem;
}

/* .contact .row .form-container .inputBox {
  width: 100%;
  display: flex;
  justify-content: space-between;
} */

.contact .row .form-container .names-container {
  display: flex;
  gap: 1rem;

}
.contact .row .form-container .names-container .first-name,.last-name  {
  width: 50%;
}
.contact .row .form-container .names-container .last-name {
  width: 50%;
}

.inputBox input {
  width: 100%;
}

input[type="email"],
textarea {
  width: 100%;
  box-sizing: border-box;
}

textarea {
  height: 20rem;
  padding: 1rem;
  resize: none;
}

button {
  background-color: var(--color);
  color: #fff;
  cursor: pointer;
}

button:hover {
  opacity: .8;
}

.contact .row .form-container .error {
  color: red;
  font-size: 1rem;
}

.contact .row .form-container .success {
  color: green;
  margin-top: 10px;
  font-size: 1rem;
}

.faq {
  min-height: 100vh;
  width: 100vw;
  text-align: center;
  padding: 0 2rem;
}

.faq .row {
  display: flex;
  align-items: center;
