.footer {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 2rem;
  margin-top: 1rem;
  background: var(--color);
}

.footer h1 {
  color: #fff;
  letter-spacing: .1rem;
  font-weight: 400;
}

.footer .icons a {
  color: #fff;
  font: 1.8rem;
  padding: 0 1rem;
}






/* =========== MEDIA QUERIES ============= */
@media (max-width:768px) {
  html {
    font-size: 50%;
  }

  .header .fa-bars,
  .fa-times {
    display: block;
  }

  .header .navbar {
    position: fixed;
    top: -120%;
    left: 0;
    height: auto;
    width: 100%;
    background-color: #fff;
    z-index: 1000;
    border-top: .1rem solid rgba(0, 0, 0, 0.3);
  }

  .header .navbar ul {
    height: 100%;
    width: 100%;
    flex-flow: column;
  }

  .header .navbar ul li {
    margin: 1rem 0;
  }

  .header .navbar ul li a {
    color: #aaaaaa;
    font-size: 2.4rem;
  }

  .header .fa-times {
    transform: rotate(180deg);
    color: #fff;
    cursor: pointer;
    font-size: 3rem;
  }

  .header .navbar.active {
    top: 5.8rem;
  }

  .home .banner {
    font-size: 4rem;
  }

  .home .slogan {
    font-size: 1.7rem;
  }

  .home .wave {
    position: absolute;
  }

  .about .row {
    flex-flow: column-reverse;
    padding: 0 2rem;
  }

  .about .row .image img {
    width: 100vw;
  }

  .service .row {
    flex-flow: column-reverse;
  }

  .service .row:nth-child(even) {
    flex-flow: column;
  }

  .service .row .image img {
    width: 100vw;
  }

  .service .row .content {
    padding: 0;
  }

  .contact .row {
    flex-flow: column;
  }

  .contact .row .image img {
    width: 100vw;
  }

  .contact .row .form-container {
    width: 100%;
    padding: 0 1.5rem;
  }

  .faq {
    padding: 0;
  }

  .faq .row {
    /* padding: 0 1rem; */
    margin-bottom: 4rem;
    flex-flow: column;
  }

  .faq .row .image img {
    width: 100vw;
  }

  .faq .row .accordion-container {
    width: 100vw;
    padding: 0 1rem;
  }
}


@media(max-width:550px) {
  .footer {
    flex-flow: column;
  }

  .footer h1 {
    text-align: center;
  }

  .footer .icons {
    padding: 1rem 0;
    font-size: 1.6rem;
  }
}